import Uncheck from '@/static/img/icon-uncheck.png'
import Checked from '@/static/img/icon-checked.png'
import T from '@/utils/tips.js'
import {
	payAlipayByWap,
	payWXpayByWap
} from '@/api/payApi.js'
var vm = {
	name: 'pay',
	props: {
		show: {
			type: Boolean,
			default: false
		},
		isWx: {
			type: Boolean,
			default: true
		},
		price: {
			type: Number,
			default: 0
		},
		orderId: {
			type: String,
			default: ''
		}
	},
	data() {
		vm = this
		return {
			icon: {
				Uncheck,
				Checked
			},
			way: 0,
			resPayFrom: '',
			wxHref: '',
			checkIndex: 0
		}
	},
	created() {

	},
	mounted() {

	},
	methods: {
		wXpay() {

		},
		checkPayNav(index) {
			this.checkIndex = index
		},
		close() {
			vm.$emit('close', false)
		},
		doPay() {
			if (this.checkIndex === 0) { // 微信网页支付
				if (this.orderId !== '') {
					let data = {
						payChannelEnum: 'WEIXIN_PAY',
						orderId: this.orderId
					}
					payAlipayByWap(data).then(res => {
						if (res.code === '1000') {
							location.href = res.data;
							this.resPayFrom = ''
						} else {
							T.tips(res.message || '调用支付失败')

						}
					}).catch(err => {
						T.tips(err.message || '调用支付失败')

					})
				} else {
					T.tips('订单ID不存在')
				}

			} else { // 支付宝网页支付
				if (this.orderId !== '') {
					let data = {
						payChannelEnum: 'ALI_PAY',
						orderId: this.orderId
					}
					payAlipayByWap(data).then(res => {
						if (res.code === '1000') {
							this.wxHref = '';
							this.resPayFrom = res.data
							setTimeout(() => {
								document.forms["punchout_form"].submit()
							}, 300)
						} else {
							T.tips(res.message || '调用支付失败')
							this.resPayFrom = ''
						}
					}).catch(err => {
						T.tips(err.message || '调用支付失败')
						this.resPayFrom = ''
					})
				} else {
					T.tips('订单ID不存在')
				}

			}
			this.$emit('doPay', this.way)
		}
	}
}
export default vm
